html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  
  background-color: #fff;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  padding: 0;
}

.is-outer {
  position: relative !important; // stylelint-disable-line declaration-no-important
  left: -9999px !important; // stylelint-disable-line declaration-no-important
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important; // stylelint-disable-line declaration-no-important
}

.for-mobile {
  display: none;

  @include mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important; // stylelint-disable-line declaration-no-important
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: $maxWidthContainer;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.js-validate-error-label {
  color: #f20909 !important;
}

.js-validate-error-field {
  color: var(--color-black) !important;
  border: none !important;
}

.header-fixed {
  position: fixed;
  animation-name: show_header;
  animation-duration: .5s;
}

@keyframes show_header {
  0% {
      opacity: 0;
      top: -100px;
  }
  100% {
      opacity: 1;
      top: 0;
  }
}

.logo {
  width: 113px;
        height: 70px;
        @include tablet2 {
            width: 70px;
            height: 50px;
        }
}

.red-button {
  background-color: $colorRed;
  color: #fff;
  border-radius: 20px;
  cursor: pointer;
  transition: .3s all ease 0s;
  font-family: $heliosFont;
  text-align: center;
  text-transform: uppercase;
  font-weight: 700;
  height: 60px; 
  font-size: 20px;
  // font-size: 18px;
  &:hover {
    box-shadow: 0 8px 16px 0 rgba(92%, 0%, 0%, .2);
  }
  @include mobile {
  height: 50px;
    font-size: 15px;
}
}

.body.lock {
  overflow: hidden;
}